/* This is the css page for my App.js file */

.Container {
    width: 80%;
    margin-left:auto;
    margin-right: auto;
    background-color: #6ba3ff;
}

body {
    margin: 0;
}
.Body {
    background-color: white;
    width: 100%;
}
.ListItemComponent{
    width: 100%;
    height: 70px;
    margin: 0px;
    border-bottom: solid thin gray;
}

