.ComponentWrapper {
    padding: 15px;
}

.ComponentTitle {
    margin: 0px;
    margin-bottom: 5px;
}

.ComponentSubHeader {
    display: flex;
    flex-direction: row;
    margin: 0px;
    list-style-type: none;
    padding: 0px;
}

.ComponentSubHeader li {
    margin-right: 5px;
    font-size: 12px;
    border-right: solid thin gray;
    padding-right: 10px;
    white-space: nowrap;
    overflow: hidden;
}
.ThumbNail {
    width: 50px;
    height: 50px;
    padding: 5px;
    margin-left: 5px;
}

.SearchBar {
    display: flex;
    flex-direction: row;
    height: 60px;
    align-items: center;
}


/* This is a standard styling to each component within our search bar */
.logoName {
    margin-top: 10px;
    font-size: 15px;
}

.Link {
    position: absolute;
    top: 20px;
    margin-left: 20px;
    border: solid #6ba3ff;
    width: 15px;
    height: 15px;
    
}

.InputSearchWrapper {
    width: calc(100% - 200px);
    margin-left: 3%;
    margin-right: auto;
    display: flex;
    align-items: center;
    
}

.InputSearch {
  width: calc(100% - 280px);
  
  height: 42px;
  padding-left:60px;
  font-size: 20px;
  
    
}

.InputSearchWrapper span{
    margin-left: 8px;
}



.algoliaLogo {
    width: 65px;
    height: 65px;
    margin-left: 8px;
}

.SettingsLink {
    float: right;
    border: solid white medium;
    margin-right: 20px;
    width: 10px;
    height: 10px;
}

